import React from 'react';
import Slider from "react-slick";
import ApiProvider from "../providers/api_provider";
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'

class Banners extends React.Component {
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }

    settingsSlider = {
        dots: false,
        infinite: false,
        speed: 500,
        arrows: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        onInit: () => {
            this.setState({
                sliderInit: true,
            })
        },
        beforeChange: (current, next) => {
            this.changeSliderIndex(current, next);
        },
    };

    state = {
        pageLoading: true,
        banners: [],
        sliderInit: false,
    }

    loadData = async () => {
        const banners = await ApiProvider.getBanners();
        this.setState({
            pageLoading: false,
            banners: banners,
        });
    }

    componentDidMount() {
        this.loadData();
    }

    next() {
        if (this.slider !== undefined) {
            this.slider.slickNext();
        }
    }

    previous() {
        if (this.slider !== undefined) {
            this.slider.slickPrev();
        }
    }

    changeSliderIndex = (current, next) => {
        if (current === 0 && next !== 0) {
            document.querySelector('.banner-prev-arrow').classList.remove('disabled');
        } else if (current !== 0 && next === 0) {
            document.querySelector('.banner-prev-arrow').classList.add('disabled');
        }
        if ((next + this.slider.props.slidesToShow) === this.state.banners.length) {
            document.querySelector('.banner-next-arrow').classList.add('disabled');
        } else {
            document.querySelector('.banner-next-arrow').classList.remove('disabled');
        }
    }

    render() {
        return (
            <div className="banner__wrapper">
                {this.state.pageLoading && <div className="banner__item-stub"/>}
                {(!this.state.pageLoading && this.state.banners.length > 0) && this.renderBanners()}
                {this.state.sliderInit && <div className="banner__slider-arrows">
                    <button className="banner__slider-arrow banner-prev-arrow disabled"
                            onClick={this.previous}/>
                    <button className="banner__slider-arrow banner-next-arrow" onClick={this.next}/>
                </div>}
            </div>
        )
    }

    renderBanner = (banner) => {
        return (
            <div className="banner__item">
                <div className="banner__item-bg">
                    <img src={banner.file} alt={banner.name} />
                </div>
                <div className="banner__item-content">
                    <div className="wrapper">
                        <div className="banner__item-name h1">{banner.name}</div>
                    </div>
                </div>
            </div>
        )
    }

    renderBanners = () => {
        const banners = [];
        for (const banner of this.state.banners) {
            banners.push(this.renderBanner(banner));
        }

        return (
            <Slider {...this.settingsSlider}>
                {banners}
            </Slider>
        )
    }
}

export default Banners;